$fonts: ("lexend":"Lexend","worksans":"Work Sans");

.font-lexend{
  font-family: "Lexend", sans-serif !important;
}

.font-worksans{
  font-family: "Work Sans", sans-serif !important;
}

.yid{
  &-card{
    &-proc{
      &-book{
        border:1px solid red !important;
        position:-webkit-sticky !important;
        position:sticky !important;
        top:140px !important;
      }
    }
  }
  &-badge{
    &-location{
      position:absolute;
      top:10px;
      right:10px;
      transition:.2s;
      z-index: 2;
      display: flex;
      align-items: center;
      overflow: hidden;
      border-radius:20px;
      gap:0 !important;
      background:transparent !important;
      cursor: default;
      div{
        cursor: pointer;
        width:32px !important;
        height:32px !important;
        min-width: 32px !important;
        min-height:32px !important;
      }
      &:hover{
        transition:.2s;
        width:120px;
        background-color:rgb(6 182 212 / var(--tw-bg-opacity)) !important;
        p{
          color:#fff;
        }
      }
      p{
        margin-left: 5px;
        font-size: 14px;
        text-transform: capitalize;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
  }
  &-loc{
    overflow: hidden;
    &:hover{
      transition:.2s;
      width:calc(75% - 20px);
      &:before{
        transition:.2s;
        opacity: 1;
      }
      &:after{
        -webkit-transform: scale(0);
                transform: scale(0);
        transition:.2s;
      }
      .yid-loc{
        &-icon{
          transition:.2s;
          font-size: 20px !important;
          a{
            color:rgb(33,185,228);
          }
        }
      }
    }
    &-icon{
      z-index: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      height:40px !important;
      min-width:40px !important;
      font-size: 16px !important;
      a {
        color:rgb(255,255,255);
      }
    }
    &-content{
      z-index: 1;
      margin:0 5px;
      width:calc(100% - 60px);
    }
    transition:.2s;
    width:40px;
    display: flex;
    height:40px;
    align-items: center;
    padding:0 !important;
    background:transparent !important;
    &:after{
      content:"";
      position:absolute;
      top:2px;
      border-radius:100%;
      left: 2px;
      width:36px;
      height:36px;
      z-index: 0;
      opacity: .9;
      background:rgb(33,185,228);
    }
    &:before{
      content: '';
      position:absolute;
      transition:.2s;
      top:0;
      left: 0;
      width:100%;
      height:100%;
      z-index: 0;
      opacity: 0.5;
      border-radius:20px;
      background:white;
    }
  }
}