.loading.cover-content {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.loading.cover-page {
  position: fixed;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.yid-card-proc-book {
  position: -webkit-sticky !important;
  position: sticky !important;
  top: 140px !important;
}
.yid-loc {
  overflow: hidden;
  transition: 0.2s;
  width: 40px;
  display: flex;
  height: 40px;
  align-items: center;
  padding: 0 !important;
  background: transparent !important;
}
.yid-loc:hover {
  transition: 0.2s;
  width: calc(75% - 20px);
}
.yid-loc:hover:before {
  transition: 0.2s;
  opacity: 1;
}
.yid-loc:hover:after {
  -webkit-transform: scale(0);
          transform: scale(0);
  transition: 0.2s;
}
.yid-loc:hover .yid-loc-icon {
  transition: 0.2s;
  font-size: 20px !important;
}
.yid-loc:hover .yid-loc-icon a {
  color: #21b9e4;
}
.yid-loc-icon {
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px !important;
  min-width: 40px !important;
  font-size: 16px !important;
}
.yid-loc-icon a {
  color: #ffffff;
}
.yid-loc-content {
  z-index: 1;
  margin: 0 5px;
  width: calc(100% - 60px);
}
.yid-loc:after {
  content: "";
  position: absolute;
  top: 2px;
  border-radius: 100%;
  left: 2px;
  width: 36px;
  height: 36px;
  z-index: 0;
  opacity: 0.9;
  background: #21b9e4;
}
.yid-loc:before {
  content: '';
  position: absolute;
  transition: 0.2s;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  opacity: 0.5;
  border-radius: 20px;
  background: white;
}
.loading .ant-spin {
  color: #3e79f7;
}
